import React, { useEffect } from 'react';
import { Button, Input } from 'reactstrap';

export const Rep = ({ item, onItemChange }) => {
  useEffect(() => {
    Object.keys(item).map(key => console.log(key + ' -> ' + item[key].value));
  }, []);
  if (!item.id) console.log("Data item must contain 'id' field");
  if (!item.editable) console.log("Data item must contain 'id' field");


  return (
    <tr>
      {Object.keys(item).map(key => {
        if (key === 'id') {
          return <th scope="row">{item[key].value}</th>;
        }
        return (
          <td>
            {!item[key].editable ? (
              <Input
                type={item[key].type}
                value={item[key].value}
                onChange={event =>
                  onItemChange({
                    ...item,
                    key: { value: event.target.value, type: item[key].type },
                  })
                }
              />
            ) : (
              item[key].value
            )}
          </td>
        );
      })}
      <td>
        <Button
          color={item.editable ? 'primary' : 'danger'}
          onClick={event => onItemChange({ ...item, editable: !item.editable })}
        >
          {item.editable ? 'edit' : 'done'}
        </Button>
      </td>
    </tr>
  );
};
